.container {
  width: 300px;
  height: calc(100vh - 60px);
  overflow-x: hidden;
  overflow-y: auto;
  background-color: #e0e0e0;
  box-shadow: 6px 6px 12px #bebebe, -6px 3px 12px #ffffff;
  position: fixed;
  top: 60px;
  z-index: 1;
  padding: 1rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  transition: all 0.3s linear;
}
.dropDownContainer {
  width: 100%;
  margin-bottom: 1rem;
}
.dropDownTrigger {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  background-color: white;
  font-weight: 700;
  -ms-user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  cursor: pointer;
  transition: all 0.3s linear;
}
.dropDownTrigger:hover {
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.5);
}
.dropDownTriggerActive {
  background-color: var(--s);
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.5);
  color: white;
}
.dropDown {
  transition: all 0.2s linear;
  transform-origin: top left;
  transform: scale(0);
}
.dropDownActive {
  padding-top: 1rem;
  transform: scale(1);
}
.dropDown button {
  width: 100%;
  height: 40px;
  border: none;
  outline: none;
  cursor: pointer;
  font-weight: 700;
  background-color: white;
  margin-bottom: 1rem;
  transition: box-shadow 0.3s linear;
}
.dropDown button:hover {
  background-color: var(--p);
  color: white;
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.5);
}
.dropDown button:last-child,
.dropDownContainer:last-child {
  margin-bottom: 0rem;
}
@media screen and (max-height: 420px) {
  .container {
    top: 0px;
    height: 100vh;
    border-top: none;
    padding-bottom: calc(1rem + 50px);
    z-index: 100;
  }
}
